<div class="border bg-opacity-100 bg-gray-700 transport p-4"><div class="flex align-middle"><div class="flex-0 flex items-center mr-3"><button class="border rounded-full h-14 w-14 text-xl hover:text-gray-400 hover:border-gray-400" v-if="!this.playing" :disabled="!loaded" @click="onPlay">&#x25b6;</button><button class="border rounded-full h-14 w-14 text-xl text-green-300 border-green-300 animate-pulse hover:text-green-400 hover:border-green-400" v-if="this.playing" :disabled="!loaded" @click="onPlay">&#x25b6;</button></div><div class="flex-1 flex align-left"><button class="border rounded-full h-14 w-14 text-xl hover:text-gray-400 hover:border-gray-400" :disabled="!loaded" @click="onStop">&#x25a0;</button></div><ul class="flex-initial"><li> <b>File Duration: </b><p>{{ duration }}</p></li><li> <b>Sample Length: </b><p>{{ sampleDuration }}</p></li><li> <b>Sample Rate:</b><p>{{ sampleRate }}</p></li></ul></div></div>