<div class="h-screen"><div v-if="!loaded"><h1 class="title">(Ain't No) Scrub</h1><div class="px-4"><Upload v-model="url"></Upload><p class="my-4 text-center">or</p><Record v-model="url"></Record></div></div><div v-else><div class="main"><h1 class="title">(Ain't No) Scrub</h1><div><Visualiser :audioContext="audioContext" :audioBuffer="rawBuffer" :playing="playing" :loaded="loaded"></Visualiser></div><div class="m-4 text-center rounded border bg-gray-500"><h2 class="p-4 text-xxl text-gray-300 text-left text-shadow-md">Mixer</h2><div><div class="pt-4"><AudioTrack v-for="trackIndex in trackCount" :key="trackIndex" :color="colorMap[trackIndex%5]" :trackNumber="trackIndex" :audioContext="audioContext" :audioBuffer="rawBuffer" :playing="playing" :stop="stop"></AudioTrack></div></div><div class="flex m-3"><button class="w-full uppercase bg-gray-100 pl-4 font-semibold text-center hover:bg-gray-300 hover:border-gray-300" @click="onTrackAdd">Add Audio Track</button></div></div></div><div class="m-0 w-screen fixed bottom-0 z-100 object-bottom"><Transport :audioContext="audioContext" :audioBuffer="rawBuffer" :playing="playing" :loaded="loaded" @onPlay="onPlay" @onStop="onStop"></Transport></div></div></div>