<div class="m-4 text-left border rounded bg-gray-200"><div class="p-2 text-align-left align-middle items-center flex"><div class="p-1 flex flex-0"><a @click="collapsed = !collapsed"><h2 class="inline" v-if="!collapsed">&#x25BE;</h2><h2 class="inline" v-else>&#x25B8;</h2></a></div><div class="p-1 flex flex-0"><button class="border rounded-full h-5 w-5 text-xs inline text-green-500 border-green-500 hover:text-green-600 hover:border-green-600" v-if="enabled" @click="onPowerOff"><fa class="power-off" icon="power-off"></fa></button><button class="border rounded-full h-5 w-5 text-xs inline text-red-500 border-red-500 hover:text-red-600 hover:border-red-600" v-if="!enabled" @click="onPowerOff"><fa class="power-off" icon="power-off"></fa></button></div><h2 class="inline">Track {{ trackNumber }}</h2><div class="pl-4"><button class="h-6 w-6 disabled:opacity-50" v-if="!previewPlaying" @click="trigger" :disabled="!enabled"><fa class="volume-up hover:text-gray-600" icon="volume-up"></fa></button><button class="h-6 w-6 animate-pulse disabled:opacity-50" v-if="previewPlaying" @click="trigger" :disabled="!enabled"><fa class="volume-up hover:text-gray-600" icon="volume-up"></fa></button></div></div><div v-if="!collapsed"><div class="p-2"><label>Playback Speed</label><VueSlider v-model="playbackSpeed" min="0" max="10" interval="0.001"></VueSlider></div><div class="p-2"><label>Pan</label><VueSlider v-model="panLocation" min="-1" max="1" interval="0.1"></VueSlider></div><div class="p-2"><label>Volume</label><VueSlider v-model="gain" min="0" max="1" interval="0.1"></VueSlider></div><div class="p-2 hidden"><label>Predelay (s)</label><VueSlider v-model="predelay" min="0" max="5" interval="0.1"></VueSlider></div><div class="p-2"><label>Playback Locators</label><VueSlider v-model="locator" min="0" max="1" interval="0.0001"></VueSlider><label>Duration: {{ ((duration * 100) | round) / 100 }}</label><label>; Normalized Duration: {{ (((duration * 100) / playbackSpeed) | round) / 100 }}</label></div><div class="p-2"><label>Looped</label><input class="m-2" type="checkbox" v-model="loop"></div></div></div>