<div class="w-full h-full"><form enctype="multipart/form-data" novalidate><label class="file-select block h-64 relative overflow-hidden" for="audio-upload"><input class="overlayed" id="audio-upload" type="file" single name="uploadAudioFile" @change="uploadFile($event.target.files)" accept="audio/*"><span class="overlayed w-full border-2 pointer-events-none flex justify-center items-center bg-gray-100 align-middle h-full normal-case"><div class="text-center"><slot><p>Drag and Drop an Audio File</p></slot></div></span></label></form></div>